.FAQ{
    display: flex;
    min-height: 100vh;
.faqcontent{
  margin-left: 300px;
  margin-right: 300px;
  @media only screen and (max-width: 1280px) {
    margin-left: 200px;
  margin-right: 200px;
  }
  @media only screen and (max-width: 768px) {
    margin-left: 20px;
    margin-right: 20px;
  }
}

    h2{
      padding: 0;
      margin: 0;
      font-family: Teko;
      font-size: 150px;
    }
    img{
      background-repeat: no-repeat;
      bottom: 0;
      right: 50%;
      transform: translateX(10%);
      width: 100vh;
      height: auto;
      z-index: 0;
      @media only screen and (max-width: 1280px) {
        right: 50%;
        transform: translateX(-10%);
      }
      @media only screen and (max-width: 768px) {
        visibility: hidden;
      }
    }
}
