.VerifyNft{
    position: relative;
    min-height: 100vh;
    display: block;
    overflow: hidden;
    z-index: -1;

    img{
       padding-top: 20vh;
       margin-left: auto;
       margin-right: auto;
       width: 300px;
       position: absolute;
    background-repeat: no-repeat;
    top: 0;
    left: 50%;
    transform: translateX(-50%);
    height: auto;
    z-index: 0;
    }

}

