  min-height: 100%; 
  object-fit: cover;
  
  }
.parallax-text3{
  cursor: default;
  position: absolute;
  text-decoration: underline ;
  top: 50%;
  left: 50%;
  text-align: center;
  transform: translate(-50%,-50%);
  font-size: 15vh;
  font-weight: 900;
  margin-left: auto;
  margin-right: auto;
  font-style: italic;
  color: #ffffff;
  z-index:0;  
  @media only screen and (max-width: 1280px) {
    font-size: 20vh;
  }
  @media only screen and (max-width: 1024px) {
    font-size: 20vh;
  }
  @media only screen and (max-width: 768px) {
    font-size: 4vh;
  }
}
.subtitle-text3{
  position: absolute;
  text-align: center;
  top: 40%;
  left: 50%;
  transform: translate(-50%,-50%);
  font-size: 2vh;
