  margin-left: auto;
  margin-right: auto;
  font-style: italic;
  color: #ffffff;
  z-index:1;  
  @media only screen and (max-width: 1280px) {
    top: 10%;
  }
  @media only screen and (max-width: 1024px) {
    top: 20%;
  }
  @media only screen and (max-width: 768px) {
    top: 40%;
  }
}
}


