.home6{
  overflow: hidden;
  .home6_1{
section {
  width: 100%;
}

article {
  display: flex;
  width: 400%;
  animation: bannermove 40s linear infinite;
}

article.paused {
  -webkit-animation-play-state: paused;
  animation-play-state: paused;
}

div {
  width: 100%;
}

ul {
  display: flex;
  list-style-type: none;
  padding-left: 0;
  margin: 0;
}
img{
  padding-left: 40px;
}

@keyframes bannermove {
  0% {
    transform: translateX(0);
  }
  100% {
    transform: translateX(-50%);
  }
}

@media only screen and (max-width: 1280px) {
  article {
    width: 500%;
  }
}
@media only screen and (max-width: 1024px) {
  article {
    width: 600%;
  }
}
@media only screen and (max-width: 768px) {
  article {
    width: 850%;
  }
}
}

.home6_2{
  section {
    width: 100%;
    overflow: hidden;
  }
  
  article {
    display: flex;
    width: 400%;
    animation: bannermove2 40s linear infinite;
  }
  
  article.paused {
    -webkit-animation-play-state: paused;
    animation-play-state: paused;
  }
  
  div {
    width: 100%;
  }
  
  ul {
    display: flex;
    list-style-type: none;
    padding-left: 0;
    margin: 0;
  }
  img{
    padding-top: 100px;
    padding-left: 100px;
  }
  
  @keyframes bannermove2 {
    100% {
      transform: translateX(0);
    }
    0% {
      transform: translateX(-50%);
    }
  }

  @media only screen and (max-width: 1280px) {
    article {
      width: 500%;
    }
  }
  @media only screen and (max-width: 1024px) {
    article {
      width: 600%;
    }
  }
  @media only screen and (max-width: 768px) {
    article {
      width: 950%;
    }
  }
}

