

.team{
  h2{
    padding: 20px;
    padding-top: 100px;
    font-size: 100px;
    font-weight: 900;
    text-align: center;
    color: #d6d6d6;
  }
  
  .contents{
    display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  flex-wrap: wrap;
  
.container{
  position: relative;
  display: flex;
  justify-content: center;
  align-items: flex-end;
  width: 320px; 
  height: 450px;
  margin: 1rem;
  background-position: center;
  box-shadow: 2px 8px 6px #1b1b1b;
  overflow: hidden;
}

.card{   
  position: absolute;
  width:  100%;
  top: 35rem;
  height: 40%;
  color: #d6d6d6;
  text-align: center;
  background: transparent;
  backdrop-filter: blur(8px);
  transition: all .5s ease; 
}

.container:hover .card{
  top: 17rem;
  
} 

.text{
  font-size: 25px;
  transition: all .5s ease;
}
h5{
  margin: 1rem;
}
button{
  padding: .5rem;
  background: #000000;
  border: none;
  color: #fff;
  margin: 1rem;
  cursor: pointer;
}

}
}